import { axiosJmaster } from "../util/axios";

export const meAPI = () => {

  var config = {
    method: 'get',
    url: '/member/me',
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem("token")
    }
  };

  return axiosJmaster(config)
    .then(function (response) {
      return (response.data);
    })
}