import { useContext } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { AuthContext } from "../../context/AuthContext";
import { ThemeContext } from "../../context/ThemeContext";
import { useMe } from "../../hook/useAuth";
import { incrementAsync } from "../../redux/counterSlice";

function Menu() {
  let { mode, setMode } = useContext(ThemeContext);
  let { setAuth, user } = useContext(AuthContext);
  //co the xai ham hook
  const u = useMe();//user la tra ve tu ham

  const counter = useSelector((store) => store.counter);
  const dispatch = useDispatch();

  console.log(counter);

  let handleMode = () => {
    setMode(mode === 'light' ? 'dark' : 'light');
    // dispatch(increment());//goi action co type increatment
    // dispatch(incNumber(10)); //action = {type="counter/incNumber", payload:10}
    dispatch(incrementAsync(10));
  };
  let handleLogout = () => {
    setAuth(false);
    localStorage.removeItem("token");
  };
  return (
    <ul>
      <li>Xin chao {u.name} - {counter.x}</li>
      <li>
        <button onClick={handleMode}>{mode}</button>
      </li>
      <li>
        <Link to="/">Home</Link>
      </li>
      <li>
        <Link to="/categories">Category</Link>
      </li>
      <li>
        <Link to="/user">User</Link>
      </li>
      <li>
        <Link to="/posts">Posts</Link>
      </li>
      <li>
        <button onClick={handleLogout}>Logout</button>
      </li>
    </ul>
  );
}
export default Menu;