import { Pagination } from "@mui/material";
import { Stack } from "@mui/system";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { searchThunk } from "../../redux/categorySlice";
import { deleteCategoryAPI } from "../../service/category.service";

function ListCategory() {
  let navigate = useNavigate();
  const dispatch = useDispatch();

  let {
    categories,
    recordsFiltered,
    totalPage
  } = useSelector((store) => store.category);

  // let [categories, setCategories] = useState([]);

  let [search, setSearch] = useState({
    "start": 0,
    "length": 5,
    "search": {
      "value": ""
    }
  });

  // let [totalPage, setTotalPage] = useState(0);

  useEffect(() => {
    fetchData();
    console.log("online");
    //unmount
    return () => {
      console.log("offline");
    };
  }, [search]);

  const fetchData = async () => {
    dispatch(searchThunk(search));
  };

  const handleSearch = (e) => {
    setSearch({
      ...search,
      "search": {
        "value": e.target.value
      }
    });
  };

  const handleNext = () => {
    setSearch({
      ...search,
      start: search.start + search.length
    });
  };

  const handlePrev = () => {
    setSearch({
      ...search,
      start: search.start > 0 ? search.start - search.length : 0
    });
  };

  const handleChangeSize = (e) => {
    const size = e.target.value;
    setSearch({
      ...search,
      length: parseInt(size)
    });
  };

  const handlePage = (index) => {
    setSearch({
      ...search,
      start: index * search.length
    });
  };

  const handleDelete = async (id) => {
    try {
      await deleteCategoryAPI(id);
      fetchData();
      toast("Xoa thanh cong danh muc!",
        {
          type: 'success'
        });
    } catch (e) {
      toast("Xoa that bai danh muc!", {
        type: 'error'
      });
      console.log(e);
    }
  };

  return <div>
    <Link to="/category/create">Create Category</Link>
    <div>
      <input onChange={handleSearch} />
    </div>
    <table border={1}>
      <thead>
        <tr>
          <th>ID</th>
          <th>Name</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {categories.map(c => {
          return (
            <tr key={c.id}>
              <td>{c.id}</td>
              <td>{c.name}</td>
              <td><button onClick={() => handleDelete(c.id)}>Delete</button>
                | <button onClick={() => navigate(`/category/update/${c.id}`)}>Edit</button></td>
            </tr>
          );
        })}
      </tbody>
    </table>
    <Stack direction={"row"} alignItems={"center"}>
      <select onChange={handleChangeSize}>
        <option value="5">5</option>
        <option value="10">10</option>
        <option value="20">20</option>
      </select>

      {/* <button onClick={handlePrev}>Prev</button>
      {
        Array.apply(null, { length: totalPage }).map((_, index) => {
          return (
            <button key={index} onClick={() => handlePage(index)}>{index}</button>
          )
        })
      }
      <button onClick={handleNext}>Next</button> */}

      <Pagination count={totalPage} variant="outlined" color="primary" showFirstButton showLastButton
        onChange={(e, value) => handlePage(value - 1)}
      />

      {/* <TablePagination
        component="div"
        count={totalPage * search.length}
        page={search.start / search.length}
        onPageChange={handlePage}
        rowsPerPage={search.length}
        onRowsPerPageChange={handleChangeSize}
      /> */}

      {/* <p>Tong so trang: {totalPage}</p> */}
    </Stack>
  </div>;
}

export default ListCategory;